.Explore {
  color: white;
  width: 100vw;
  height: auto;
}
.Explore {
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}



